.gpt3__whatisGPT3 {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt3__whatisGPT3-feature{
    display: flex;
}

.gpt3__whatisGPT3-feature .gpt3__feature-containers__feature{
    margin: 1rem;
}

.gpt3__whatisGPT3-feature .gpt3__feature-containers__text{
    max-width: 700px;
}

.gpt3__whatisGPT3-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 3rem 1rem 2rem;
}

.gpt3__whatisGPT3-heading h1{
    max-width: 510px;

    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__whatisGPT3-heading p{
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;

    color: var(--color-subtext);
    cursor: pointer;
}

.gpt3__whatisGPT3-containers{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin-top: 2rem;
}

/* Customizing feature component as per needs */
.gpt3__whatisGPT3-containers .gpt3__feature-containers__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3__whatisGPT3-containers .gpt3__feature-containers_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatisGPT3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatisGPT3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatisGPT3-feature .gpt3__feature-containers__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatisGPT3-feature .gpt3__feature-containers_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatisGPT3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatisGPT3-containers .gpt3__feature-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}