.gpt3__blog{
    display: flex;
    flex-direction: column;
}

.gpt3__blog-title{
    width: 100%;
    margin-bottom: 5rem;
}

.gpt3__blog-title h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: left;

    max-width: 700px;
}

.gpt3__blog-container{
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }

    .gpt3__blog-container_groupA{
        margin-top: 2rem;
        width: 48%;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article-cover{
        height: 200px;
    }

}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_groupB {  
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container_groupA {
        width: 100%;
    }

    .gpt3__blog-title h1{
        font-size: 48px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-title h1{
        font-size: 34px;
        line-height: 42px;
    }
}